							},
							// headers: {
							// 	token: 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJwaG9uZU51bWJlciI6ODg5MTk3ODA4NSwiaWF0IjoxNjE5NTk2NDQyfQ.ivLx4XVN8wVcyA6_90rCyWU8LeN3XIsrr7oMleWSCqQ'
							// },
						}
						console.log(details)
            this.$axios(details)
							.then((response) => {
								this.V_numbers = response.data.numbers
								this.timerCount = response.data.Seconds
								this.listingId = response.data.listingId
								console.log(response)
								console.log(response.data.numbers)
							})
							.catch((error) => {
								console.error(error);
							})

					}
				},
